
.green{
  width: 100%;
  height: 60vh;
  background-color: green;
}

.box img{
  width: 25%;
  height: 200px;
  object-fit: cover;
  cursor: pointer;

}

.see{
  display: flex;
  justify-content: space-around;
  background-color: black;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .box img{
    width: 50%;
  }
  .green{
    height: 30vh;
  }
}

@media screen and (min-width:500px) and (max-width: 800px) {
  .box img{
  width: 33.3333333333333%;

  }
  .green{
    height: 45vh;
  }
}


